import React, { useEffect } from 'react'

const Toolbar = (props) => {

    const toogleContrast = (btn) => {
        if (window.sessionStorage) {
            alert(btn);
            const contrast = "contrast"
            const current = window.sessionStorage.getItem(contrast)
            window.sessionStorage.setItem(contrast, !JSON.parse(current || true))
        }
    }

    useEffect(() => {
        toogleContrast(false)
    }, [])

    return (
        <React.Fragment>
            <div className="app-toolbar">
                <div className="app-toolbar--item">A+</div>
                <div className="app-toolbar--item">A-</div>
                <div className="app-toolbar--item" onClick={() => toogleContrast(true)}>Wersja kontrastowa</div>
            </div>
        </React.Fragment>
    )
}

export default Toolbar